.item-enter {
    opacity: 0;
  }
  .item-enter-active {
    opacity: 1;
    transition: opacity 500ms ease-in;
  }
  .item-exit {
    opacity: 1;
  }
  .item-exit-active {
    opacity: 0;
    transition: opacity 500ms ease-in;
  }

  .scrollhost_container {
    overflow: scroll;
    overflow-x: hidden;
    height: 100%;
}

.scrollhost_container::-webkit-scrollbar {
    position: absolute;
    width: 10px;
}

.scrollhost_container::-webkit-scrollbar-track {
    background: transparent; 
}

.scrollhost_container::-webkit-scrollbar-track {
    background: transparent; 
}

.scrollhost_container:hover::-webkit-scrollbar-thumb {
    /*background: #4A4A4A; */
    background: #454952;
    border-radius: 8px;
}